@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
a {
    text-decoration: none;
}
.container {
    margin: 0 200px;
    height: 100%;
}
body {
    background-color: #19303f;
}
.contacto {
    height: 72vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.h5 {
    color: #fff;
    font-size: 50px;
    font-weight: normal;
    margin-bottom: 20px;
}
label {
    color: #fff;
    text-align: center;
    margin-top: 10px;
}
.contac {
    color: #CD5A69;
}
input {
    margin-top: 10px;
    height: 40px;
    border-radius: 20px;
    font-weight: bold;
    font-size: 20px;
    text-align: center;
    border: none;
}
.send {
    display: flex;
    align-items: center;
    justify-content: center;
}
input:focus {
    border: none;
    outline: none;
}
form {
    display: flex;
    flex-direction: column;
    width: 500px;
}
textarea {
    margin-top: 10px;
    border-radius: 20px;
    max-height: 200px;
    min-height: 100px;
    max-width: 500px;
    min-width: 500px;
    border: none;
    outline: none;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
}
textarea:focus {
    border: none;
    outline: none;
}
.btn-contact {
    width: 300px;
    margin-top: 20px;
    background-color: #CD5A69;
    outline: none;
    border-radius: 30px;
    border: none;
    padding: 7px 50px;
    color: #fff;
    font-size: 20px;
    font-weight: normal;
    transition: all 0.5s ease;
}
.btn-contact:hover  {
    background-color: rgb(0, 0, 0);
    color: #CD5A69;
    font-weight: normal;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    border: none;
    outline: none;
}

.recaptcha {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media  screen and (max-width: 1440px) {
    .container {
        height: 100%!important
    }
}

@media  screen and (max-width: 1050px) {
    .h5 {
        font-size: 35px;
    }
}
@media  screen and (max-width : 768px) {
    .container {
        margin: 0 40px;
    }
    .h5 {
        font-size: 27px;
        
    }
    form {
        width: 400px;
    }
    textarea {
        max-height: 200px;
        min-height: 100px;
        max-width: 400px;
        min-width: 400px;
    }
}
@media  screen and (max-width : 430px) {
    .h5 {
        font-size: 18px;
        
    }
    form {
        width: 300px;
    }
    textarea {
        max-width: 300px;
        min-width: 300px;
    }
}
@media screen and (max-width:319px){
    .h5 {
        font-size: 18px;
        width: 100%;
        margin-left: 50px;
    }
    form {
        width: 250px;
    }
    textarea {
        max-width: 250px;
        min-width: 250px;
    }
    
}
