@media  screen and (max-width: 1440px) {
    .container {
        height: 100%!important
    }
}

@media  screen and (max-width: 1050px) {
    .h5 {
        font-size: 35px;
    }
}
@media  screen and (max-width : 768px) {
    .container {
        margin: 0 40px;
    }
    .h5 {
        font-size: 27px;
        
    }
    form {
        width: 400px;
    }
    textarea {
        max-height: 200px;
        min-height: 100px;
        max-width: 400px;
        min-width: 400px;
    }
}
@media  screen and (max-width : 430px) {
    .h5 {
        font-size: 18px;
        
    }
    form {
        width: 300px;
    }
    textarea {
        max-width: 300px;
        min-width: 300px;
    }
}
@media screen and (max-width:319px){
    .h5 {
        font-size: 18px;
        width: 100%;
        margin-left: 50px;
    }
    form {
        width: 250px;
    }
    textarea {
        max-width: 250px;
        min-width: 250px;
    }
    
}